import React from 'react';
import { MDBContainer, MDBDataTable, MDBTable, MDBTableBody, MDBTableHead } from 'mdbreact';
import $ from 'jquery';


const UserDataTable = ({ posts }) => {
    const data = {
        columns: [
            {
                label: '#',
                field: 'id',
                sort: 'disabled',
                width: 1
            }, 
            {
                label: 'Email',
                field: 'email',
                sort: 'dsc',
                width: '40'
            }, 
            {
                label: 'Role',
                field: 'role',
                sort: 'asc',
                width: '40'
            },
            {
                label: 'Created On',
                field: 'createdOn',
                sort: 'asc',
                width: 40
            },
            {
                label: 'Action',
                field: 'action',
                sort: 'disabled',
                width: 50,
                height: 5
            }
        ],
                rows: posts
    };

    return (
        <MDBContainer>
            <MDBDataTable 
                id="idforSubscription"
                small= {true} 
                theadColor = "white" 
                responsiveXl 
                btn={true}
                striped 
                data={data } 
                sortable={ true } 
                bordered
                noBottomColumns
                responsiveSm             
            />
        </MDBContainer>
    );
};

export default UserDataTable;