import React, { useState, Component } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter, Spinner, Col, FormGroup, Form, Label, Input, Card } from 'reactstrap';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import * as APP_CONFIG from '../../config/config';
import axios from 'axios';
import Switch from '@coreui/react/lib/Switch';


toast.configure()

const CreateUser = (props) => {
    const {
        buttonLabelView,
        className,
        errorInput= ""
    } = props;

    const [modal, setModal] = useState(false);

    function addCreateUserClick(e) {
        e.preventDefault();

        const currentUser = JSON.parse(localStorage.getItem("currentUser"));
        const createdBy = currentUser?.role;

        let formID = document.querySelector("#userData");
        const formData = new FormData(formID);

        var name = formData.get('name');
        var email = formData.get('email');
        var userRole = formData.get('userRole');

        var request = new XMLHttpRequest();

        request.onreadystatechange = function () {
            if (this.readyState === 4 & this.status === 200) {
                alert('Uploaded!');
            }
        };

        let errorInput = "* Name field may be empty";
        let errorInput1 = "* Email field may be empty";
        let errorInput2 = "* Role field may be empty";


        if(name == "" && userRole === null && email == ""){
           
            toast.error(errorInput);
            toast.error(errorInput1);
            toast.error(errorInput2);
        }
        else if(name == "" &&  email == ""){
            toast.error(errorInput);
            toast.error(errorInput1);
        }
        else if(name == "" && userRole === null ){
            toast.error(errorInput);
            toast.error(errorInput2);
        }
        else if(email == "" && userRole ===  null ){
            
            toast.error(errorInput1);
            toast.error(errorInput2);
        }
        else if(name == "" ){
            
            toast.error(errorInput);
        }
        else if(userRole === null ){
            
            toast.error(errorInput);
        }
        else{            
            const userForm = new FormData();
            userForm.append('userForm', '{"name": "' + name + '","email": " ' + email + '", "userRole": " ' + userRole + '"} ');
    
            let _that = this;
    
            axios.post(
                APP_CONFIG[APP_CONFIG.REACT_APP_ENVIRONMENT].API_CREATE_USER
                , {
                "createdBy": createdBy,
                "email": email,
                "role": userRole,
                "username": email
              },
                {
                    headers: {
                        ContentType: "Application/json",
                        Authorization: currentUser.token
                    }
                }).then(function (res) {
                    //handle success
                    toast.success('User Added Successfully');
                    axios.post(APP_CONFIG[APP_CONFIG.REACT_APP_ENVIRONMENT].API_SEND_MAIL,
                        {
                            "msg": "Hello" + email + "," + " " + "You have successfully been add to PCR Application directory." + " " + "You may login with AD crendentials with, User Role" + " " + "-" + " " + userRole,
                            "recipient": email,
                            "sender": "no-reply-PCR",
                            "subject": "Permissions Central Repository - PCR"
                        }

                    );
                    setTimeout(function () {
                        window.location.reload();
                    }, 1000)
                })
        }


    }

    // Intialize modal
    const toggle = () => setModal(!modal);
   

    return (
        <div className="container">
            {/* Button in table for Create Campaign */}
    
            <button className="btn btn-lg icon-plus" onClick={toggle} style={{ backgroundColor: '#E60000', color: '#fff', borderRadius: 10, paddingLeft: 10, paddingRight: 10, paddingTop: 5, paddingBottom: 5, float: 'right'}} role="button"><a style={{ fontFamily: 'Roboto' }}> Create User </a></button>

            <br />
            {/* Create modal for  */}
            <Modal isOpen={modal} size="lg" toggle={toggle} className={className}>
                <Form id="userData" onSubmit={addCreateUserClick}>
                    {/* modal content for the head section */}
                    <ModalHeader toggle={toggle}><a style={{ fontFamily: 'Roboto' }}> Create User </a>

                    </ModalHeader>
                    <ModalBody>
                        <ToastContainer closeButton={false} style={{ color: '#fff'}} />

                        <a style={{ color: 'red', paddingRight: '10px' }}>*</a>Required
                        <br /><br />
                        <div className="container">
                            <FormGroup row>
                                <Label htmlFor="name" sm="12" md={{ size: 2, offset: 1 }} style={{color: '#E60000'}}><a style={{ color: 'black'}}>*</a>Name:</Label>
                                <Col sm="12" md={{ size: 8, offset: 0 }} style={{ paddingLeft: '15px' }}>
                                    <Input type="text" name="name" id="name" placeholder="Please provide name" required />
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="email" sm="12" md={{ size: 2, offset: 1 }} style={{color: '#E60000'}}><a style={{ color: 'black'}}>*</a>Email:</Label>
                                <Col sm="12" md={{ size: 8, offset: 0 }} style={{ paddingLeft: '15px' }}>
                                    <Input type="email" name="email" id="email" placeholder="Please provide email" required />
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="userRole" sm="12" md={{ size: 2, offset: 1 }} style={{color: '#E60000'}}><a style={{ color: 'black'}}>*</a>Role:</Label>
                                <Col sm="12" md={{ size: 8, offset: 0 }} >
                                    <Input type="select" name="userRole" id="userRole" defaultValue="Please select user role" required> 
                                        <option disabled>Please select user role</option>
                                        <option value="ADMIN">Admin</option>
                                        <option value="USER">Regular User</option>
                                        <option value="SUPER_ADMIN">Super Admin</option>
                                    </Input>
                                </Col>
                            </FormGroup>
                            {/* <FormGroup row>
                                <Label htmlFor="description" sm="12" md={{ size: 2, offset: 1 }}>Description:</Label>
                                <Col sm="12" md={{ size: 8, offset: 0 }}>
                                    <Input type="textarea" name="description" id="description" placeholder="You may provide description" />
                                </Col>
                            </FormGroup> */}
                        </div>
                    </ModalBody>
                    <ModalFooter>
                        <div className="btn-group">
                            <a style={{ paddingRight: '0px'}}>
                                <button className="btn btn-lg" onClick={toggle} style={{ backgroundColor: '#000', color: '#fff', borderColor: 'green', borderRadius: 10, paddingLeft: 5, paddingRight: 5, paddingTop: 0, paddingBottom: 0, textTransform: 'unset' }}>Cancel</button>

                            </a>
                            <a style={{ paddingLeft: '0px'}}>
                                <button type="submit" onClick={addCreateUserClick} className="btn btn-lg" style={{ backgroundColor: '#E60000', color: '#fff', borderColor: 'green', borderRadius: 10, paddingLeft: 5, paddingRight: 5, paddingTop: 0, paddingBottom: 0, textTransform: 'unset' }} >Confirm</button>

                            </a>
                            
                        </div>
                    </ModalFooter>
                </Form>

            </Modal>

{/* 
            <Card>
                <br /><br />
                <Form id="channelData" onSubmit={addCampaignClick}><p></p> <br />
                    <div style={{ float: 'right', paddingRight: '28px' }}>
                        
                    </div><br /> 
                        <a style={{ color: 'red', paddingLeft: '26px' }} >
                            * Required
                        </a>
                        <br />
                        <div className="container">
                            <FormGroup row>
                                <Label htmlFor="name" sm="12" md={{ size: 2, offset: 1 }} >Name:<a style={{ color: 'red'}}>*</a></Label>
                                <Col sm="12" md={{ size: 8, offset: 0 }} style={{ paddingLeft: '15px' }}>
                                    <Input type="text" name="name" id="name" placeholder="Please provide name" required />
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="channel" sm="12" md={{ size: 2, offset: 1 }}>Channel:<a style={{ color: 'red' }}>*</a></Label>
                                <Col sm="12" md={{ size: 8, offset: 0 }} >
                                    <Input type="select" name="channel" id="channel" required>
                                        
                                        <option disabled >Please select channel</option>
                                        <option value="sms">SMS</option>
                                        <option value="mva App">MVA App</option>
                                        <option value="mva Web">MVA Web</option>
                                        <option value="flyTxt">Fly TxT</option>
                                    </Input>
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="description" sm="12" md={{ size: 2, offset: 1 }}>Description:<a style={{ color: 'red' }}>*</a></Label>
                                <Col sm="12" md={{ size: 8, offset: 0 }}>
                                    <Input type="textarea" name="description" id="description" placeholder="Please provide description details" required />
                                </Col>
                            </FormGroup>
                        </div>
                        
                        <br />
                        <card style={{ float: 'right'}}>
                        <br />
                        <div className="btn-group">
                            <button onClick={toggle} className="btn btn-lg" style={{ backgroundColor: '#E60000', color: '#fff', borderColor: 'green', borderRadius: 10, paddingLeft: 10, paddingRight: 10, paddingTop: 0, paddingBottom: 0, textTransform: 'unset' }} >Submit</button>
                        </div>
                    </card>
                </Form>
            </Card>
        
         */}
        </div>
    );
}

export default CreateUser;