import React, { useState } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter, Spinner, Col, FormGroup, Form, Label, Input, Card } from 'reactstrap';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import * as APP_CONFIG from '../../config/config';
import axios from 'axios';

toast.configure()

const UserActions = (props) => {
    const {
        buttonLabelView,
        className
    } = props;

    const [modal, setModal] = useState(false);
    const [modal2, setModal2] = useState(false);
    const [modal3, setModal3] = useState(false);



    // Intialize modals
    const toggle = () => setModal(!modal);
    const toggleEdit = () => setModal2(!modal2);
    const toggleDelete = () => setModal3(!modal3);

    function closeModal(e) {
        e.preventDefault();
        setModal(false);
        setModal2(false);
        setModal3(false);

    }

    let nameLength1 = props?.selectedUsername?.split('@')[0]?.split('.')[2] === undefined ? "" : (props?.selectedUsername?.split('@')[0]?.split('.')[2]);
    let nameLength2 = props?.selectedUsername?.split('@')[0]?.split('.')[3] === undefined ? "" : (props?.selectedUsername?.split('@')[0]?.split('.')[3]);

    function editUserClick(e) {
        e.preventDefault();

        let getVendorSessionId = localStorage.getItem("sessionInfoUser.id");

        let formID = document.querySelector("#userData");
        const formData = new FormData(formID);

        const currentUser = JSON.parse(localStorage.getItem("currentUser"));
        const updatedBy = currentUser?.role;

        var name = formData.get('name');
        var email = formData.get('email');
        var userRole = formData.get('userRole');
        var createdAt = new Date();
        
        var request = new XMLHttpRequest();

        request.onreadystatechange = function () {
            if (this.readyState === 4 & this.status === 200) {
                alert('Uploaded!');
            }
        };

        let errorInput = "* Name field may be empty";
        let errorInput1 = "* Email field may be empty";
        let errorInput2 = "* Role field may be empty";


        if (name == "" && userRole === null && email == "") {
   
            toast.error(errorInput);
            toast.error(errorInput1);
            toast.error(errorInput2);
        }
        else if (name == "" && email == "") {
            // alert(errorInput);
            toast.error(errorInput);
            toast.error(errorInput1);
        }
        else if (name == "" && userRole === null) {
            // alert(errorInput);
            toast.error(errorInput);
            toast.error(errorInput2);
        }
        else if (email == "" && userRole === null) {
            // alert(errorInput);
            toast.error(errorInput1);
            toast.error(errorInput2);
        }
        else if (name == "") {
            // alert(errorInput);
            toast.error(errorInput);
        }
        else if (userRole === null) {
            // alert(errorInput);
            toast.error(errorInput2);
        }
        else {
            const userForm = new FormData();
            userForm.append('userForm', '{"name": "' + name + '","email": " ' + email + '", "userRole": " ' + userRole + '"} ');

            let _that = this;


            axios.put(APP_CONFIG[APP_CONFIG.REACT_APP_ENVIRONMENT].API_UPDATE_USER,
                {
                    "active": true,
                    "createdAt": createdAt,
                    "createdBy": email,
                    "email": email,
                    "id": props.selectedUserId,
                    "role": userRole,
                    "updateAt": createdAt,
                    "updatedBy": email,
                    "username": email
                },
                {
                    headers: {
                        ContentType: "application/json",
                        Authorization: currentUser.token
                    }
                }).then(function (res) {
                    //handle success
                    toast.success('User Edited Successfully');
                    axios.post(APP_CONFIG[APP_CONFIG.REACT_APP_ENVIRONMENT].API_SEND_MAIL,
                        {
                            "msg": "Hello" + " " + email + "," + " " + "your user details was changed successfully. "  + "with user role" + " " + "-" + " " + userRole,
                            "recipient": email,
                            "sender": "no-reply-PCR",
                            "subject": "Permissions Central Repository - PCR"
                        }

                    );
                    setTimeout(function () {
                        window.location.reload();
                    }, 1000)
                })

        }

    }

    return (
        <div>
            {/* Button in table for View */}
            {/* <button className="btn btn-lg" onClick={toggle} style={{ backgroundColor: '#E60000', color: '#fff', borderColor: 'green', borderRadius: 10, paddingLeft: 5, paddingRight: 5, paddingTop: 0, paddingBottom: 0, textTransform: 'unset' }}>View</button> */}

            <a className="link" onClick={toggle} style={{ color: 'blue', fontFamily: "san-serif", paddingRight: '10px' }}>
                View
            </a>

            <a className="link" onClick={toggleEdit} style={{
                color: 'blue',
                fontFamily: "san-serif", paddingRight: '10px'
            }}>
                Edit
            </a>

            {/* <a className="link" onClick={toggleDelete} style={{ color: 'blue', textDecoration: 'underline', fontFamily: "san-serif" }}>
                Delete
            </a> */}

            {/* Edit Button */}
            {/* <button className="btn btn-lg" onClick={toggleEdit} style={{ backgroundColor: '#000', color: '#fff', borderColor: 'green', borderRadius: 5, paddingLeft: 5, paddingRight: 5, paddingTop: 0, paddingBottom: 0, textTransform: 'unset' }}> <MDBIcon icon='edit ' /> </button> */}

            {/* Delete Button  */}
            {/* <button className="btn btn-lg" onClick={toggleDelete} style={{ backgroundColor: '#E60000', color: '#fff', borderColor: 'green', borderRadius: 5, paddingLeft: 5, paddingRight: 7, paddingTop: 0, paddingBottom: 0, textTransform: 'unset' }}> <MDBIcon icon='trash-alt' /></button> */}

            {/* modal View Channel Details */}
            <Modal isOpen={modal} toggle={toggle} className={className}>
                <ModalHeader toggle={toggle}> User Profile </ModalHeader>
                <ModalBody style={{ borderRadius: '1px', borderColor: "#e60000", paddingLeft: "70px" }}>
                    <Card style={{ border: "10px" }}>
                        <a style={{ fontSize: "15px", paddingRight: "50px", color: '#E60000' }}>Name:  </a>{props.selectedUserEmail.split('@')[0]}
                        <br />
                        <a style={{ fontSize: "15px", paddingRight: "52px", color: '#E60000' }}>Email:  </a>{props.selectedUserEmail}
                        <br />
                        <a style={{ fontSize: "15px", paddingRight: "29px", color: '#E60000' }}>User Role:</a>
                        {props.selectedUserRole === "ADMIN" ? "Admin" : "" || props.selectedUserRole === "SUPER_ADMIN" ? "Super Admin" : "" || props.selectedUserRole === "USER" ? "Regular User" : "" || props.selectedUserRole === "userRoleRegular" ? "Regular User" : "" || props.selectedUserRole === "userRoleAdmin" ? "Super Admin" : ""}
                        <br />
                        <a style={{ fontSize: "15px", paddingRight: "22px", color: '#E60000' }}>Username:</a> {props.selectedUsername}
                        <br />
                        <a style={{ fontSize: "15px", paddingRight: "19px", color: '#E60000' }}>Created By: </a><a style={{ textTransform: 'capitalize' }}>
                            {props.selectedUserCreatedBy === "ADMIN" ? "Admin" : "Admin" || props.selectedUserCreatedBy === "SUPER_ADMIN" ? "Super Admin" : "Super Admin" || props.selectedUserCreatedBy === "USER" ? "Regular User" : "User"}
                        </a>
                        <br />
                    </Card>
                </ModalBody>
                <ModalFooter>
                    <div className="btn-group">
                        <button className="btn btn-lg" onClick={toggle} style={{ backgroundColor: '#E60000', color: '#fff', borderColor: 'green', borderRadius: 10, paddingLeft: 15, paddingRight: 15, paddingTop: 0, paddingBottom: 0, textTransform: 'unset' }}>OK</button>{' '}

                        {/* <button className="btn btn-lg" type="submit" style={{ backgroundColor: '#E60000', color: '#fff', borderColor: 'green', borderRadius: 10, paddingLeft: 10, paddingRight: 20, paddingTop: 0, paddingBottom: 0, textTransform: 'unset' }} >Add</button> */}
                    </div>
                </ModalFooter>
            </Modal>

            {/* Create modal for Edit */}
            <Modal isOpen={modal2} toggleEdit={toggleEdit} className={className} size="lg">
                <Form id="userData" onSubmit={editUserClick}>
                    <ModalHeader toggleEdit={toggleEdit}> Edit User </ModalHeader>
                    <ModalBody>
                        <ToastContainer closeButton={false} style={{ color: '#fff' }} />

                        {/* <a style={{ color: 'red', paddingRight: '10px' }}>*</a>Required */}
                        <br /><br />
                        <div className="container">
                            <FormGroup row>
                                <Label htmlFor="name" sm="12" md={{ size: 2, offset: 1 }} style={{ color: '#E60000' }} ><a style={{ color: 'black' }}>*</a>Name:</Label>
                                <Col sm="12" md={{ size: 8, offset: 0 }} style={{ paddingLeft: '15px' }}>
                                    <Input type="text" name="name" id="name" style={{ textTransform: 'capitalize' }} defaultValue={((props?.selectedUsername)?.split('@')[0]).split('.')[0] + " " + nameLength1 + " " + nameLength2} required />
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="email" sm="12" md={{ size: 2, offset: 1 }} style={{ color: '#E60000' }} ><a style={{ color: 'black' }}>*</a>Email:</Label>
                                <Col sm="12" md={{ size: 8, offset: 0 }} style={{ paddingLeft: '15px' }}>
                                    <Input type="email" name="email" id="email" defaultValue={props.selectedUserEmail} required />
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="userRole" sm="12" md={{ size: 2, offset: 1 }} style={{ color: '#E60000' }} required><a style={{ color: 'black' }}>*</a>Role:</Label>
                                <Col sm="12" md={{ size: 8, offset: 0 }} >
                                    <Input type="select" name="userRole" id="userRole" defaultValue={(props.selectedUserRole)}>
                                        {/* <option selected >{props.selectedUserRole === "ADMIN" ? "Admin" : "" || props.selectedUserRole === "SUPER_ADMIN" ? "Super Admin" : "" || props.selectedUserRole === "USER" ? "Regular User" : "" || props.selectedUserRole === "userRoleRegular" ? "Regular User" : "" || props.selectedUserRole === "userRoleAdmin" ? "Super Admin" : ""}
                                        </option> */}
                                        <option value="ADMIN">Admin</option>
                                        <option value="USER">Regular User</option>
                                        <option value="SUPER_ADMIN">Super Admin</option>
                                    </Input>
                                </Col>
                            </FormGroup>
                        </div>

                    </ModalBody>
                    <ModalFooter>
                        <div className="btn-group">

                            <a style={{ paddingRight: '5px' }}>
                                <button className="btn btn-lg" onClick={closeModal} style={{ backgroundColor: '#000', color: '#fff', borderColor: 'green', borderRadius: 10, paddingLeft: 5, paddingRight: 5, paddingTop: 0, paddingBottom: 0, textTransform: 'unset' }}>Cancel</button>
                            </a>

                            <button className="btn btn-lg" type="submit" onClick={editUserClick} style={{ backgroundColor: '#E60000', color: '#fff', borderColor: 'green', borderRadius: 10, paddingLeft: 5, paddingRight: 5, paddingTop: 0, paddingBottom: 0, textTransform: 'unset' }} >Submit</button>
                        </div>
                    </ModalFooter>
                </Form>
            </Modal>


            {/* Create modal for delete */}

            <Modal isOpen={modal3} toggle={toggleDelete} className={className}>
                <ModalHeader toggle={toggleDelete}>Delete</ModalHeader>
                <ModalBody>

                </ModalBody>
                <ModalFooter>
                    <div className="btn-group">
                        <button className="btn btn-lg" onClick={toggleDelete} style={{ backgroundColor: '#000', color: '#fff', borderColor: 'green', borderRadius: 10, paddingLeft: 5, paddingRight: 5, paddingTop: 0, paddingBottom: 0, textTransform: 'unset' }}>Cancel</button>{" "}{" "}{" "}

                        <button className="btn btn-lg" type="submit" style={{ backgroundColor: '#E60000', color: '#fff', borderColor: 'green', borderRadius: 10, paddingLeft: 5, paddingRight: 5, paddingTop: 0, paddingBottom: 0, textTransform: 'unset' }} >Submit</button>
                    </div>
                </ModalFooter>
            </Modal>
        </div>

    );
}

export default UserActions;