import React, { Component, Fragment } from 'react';
import "../Channel/channel.css";
import axios from 'axios';
import { MDBIcon, MDBBtn, MDBBtnGroup } from 'mdbreact';
import UserActions from './UserActions';
import UserDataTable from './UserDataTable';
import CreateUser from './CreateUser';
import {Spinner} from 'reactstrap';
import * as APP_CONFIG from '../../config/config';
import { ToastContainer, toast } from 'react-toastify';

toast.configure()

let loading = <Spinner color="danger" />;
class UserManagement extends Component {
    constructor(props) {
        super(props)
        this.state = {
            UserData: [],
            selectedSubscriptionId: "",
            loading: false,
            currentUser: null

        }
    }

    componentDidMount() {
        let _that = this;
        const currentUser = JSON.parse(localStorage.getItem("currentUser"));
        this.setState({
            currentUser: currentUser
        })
        var request = new XMLHttpRequest();
        if(currentUser.token )

        this.setState({ loading: true }, () => {
            axios.get(APP_CONFIG[APP_CONFIG.REACT_APP_ENVIRONMENT].API_GET_USER,
                {    headers: {
                        ContentType: "application/json",
                        Authorization: currentUser.token,
                  
                  }
                }
            )
                .then(response => {
                    let currentUserErrorCode = response.data.errCode;

                    let postUserResults = response.data || [];
                    let postUserItemsInitialList = postUserResults.map(function (menu) {
                       return response.data === null ? null : menu.menuItem || [];
                    });
                    let postUserItems = postUserResults;
                    
                    const postList = postUserItems.length ? (
                        postUserItems.map((post, id) => {
                            
                            let dateCreated = new Date(post.createdAt);
                            let dateUpdated = new Date(post.createdOn);
                            return (
                                {
                                    id: id+1,
                                    email: post.email,
                                    role:
                                        <Fragment>
                                            {post.role === "ADMIN" ? "Admin" : ""}
                                            {post.role === "SUPER_ADMIN" ? "Super Admin" : ""}
                                            {post.role === "USER" ? "Regular User" : ""}
                                            {post.role === "userRoleRegular" ? "Regular User" : ""}
                                            {post.role === "userRoleAdmin" ? "Super Admin" : ""}
                                        </Fragment>,
                                    createdOn: dateCreated.toString().substring(0, 28),
                                    action:
                                        <Fragment>
                                            <MDBBtnGroup>
                                                <UserActions
                                                    selectedUserId={post.id} selectedUsername={post.username} selectedUserEmail={post.email} selectedCreatedAt={post.createdAt} selectedUserUpdatedAt={post.updatedAt} selectedUserCreatedBy={post.createdBy} selectedUserActive={post.active} selectedUserRole={post.role} constructor={this.constructor} changeBackdrop={this.changeBackdrop} {...this.state} />
                                            </MDBBtnGroup>
                                        </Fragment>
                                }
                            )
                        })    
                    ) : []

                    _that.setState(
                        { 
                            UserData: postList,
                            currentUserErrorCode: currentUserErrorCode
                        }
                    )
                }).catch(function (error) {

                    if (error.response) {
                        /*
                         * The request was made and the server responded with a
                         * status code that falls out of the range of 2xx
                         */

                    } else if (error.request) {
                        /*
                         * The request was made but no response was received, `error.request`
                         * is an instance of XMLHttpRequest in the browser and an instance
                         * of http.ClientRequest in Node.js
                         */
                        toast('Error **');
                    } else {
                        // Something happened in setting up the request and triggered an Error
                        
                        toast('Error***');
                    }
                }
                );
        });

    }

    render() {

        // Sign off user
        if(this.state?.currentUserErrorCode == "2"){
            window.location.href = "/login";
            localStorage.removeItem("currentUser")
        }

        return (
            <div className="container" style={{ backgroundColor: "#fff", height: "1200px" }}>

                <CreateUser />
                <br /><br />
                <UserDataTable posts={this.state.UserData} />

            </div>
        );
    }
}

export default UserManagement;